import logo from './logo.svg';
import React, { Component } from 'react'
import './App.css';
import { library } from '@fortawesome/fontawesome-svg-core'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faTwitter } from '@fortawesome/free-brands-svg-icons' 
import { faHeart } from '@fortawesome/free-solid-svg-icons' 
import { faQuoteLeft } from '@fortawesome/free-solid-svg-icons' 
import { faQuoteRight } from '@fortawesome/free-solid-svg-icons' 
import { faGithub } from '@fortawesome/free-brands-svg-icons' 


library.add(faTwitter)
library.add(faHeart)
library.add(faQuoteLeft)
library.add(faQuoteRight)
library.add(faGithub)



class App extends Component{
  constructor(){
    super();
    this.state = {
      frase : [],
      busqueda : Math.floor(Math.random()*99),
    };
  
  this.click = this.click.bind(this);
}

  componentDidMount(){
  
    fetch('https://frann1870.pythonanywhere.com/frases')
    .then(response => response.json())
    .then(frases => this.setState({frase: frases}))
    

   }

   
   click(e) {
    this.setState({
     busqueda : Math.floor(Math.random()*99)
    });
  }

   render()  {
     const {frase, busqueda} = this.state;
     console.log(busqueda)
     console.log(this.state.frase.frases)
     const fraseFiltrada = frase.filter(frase => frase.id == busqueda) 
     console.log(fraseFiltrada.frases)
     console.log(fraseFiltrada)
     let twittear = ''
     {fraseFiltrada.map(fraseFiltrada => twittear = fraseFiltrada)}

    return (
        
      <div>
        <div className='fluid-container text-center caja' style={{position:'relative', boxShadow: '9px 11px 19px -4px rgba(100,110,120,0.87)',
        color:'#000000',borderRadius:'10px', padding : "13vh",marginTop:'15vh',marginBottom:'15vh',marginLeft:'auto',marginRight:'auto', backgroundColor: '#cfffb3', minHeight:'50vh', width:'80vw'}}>
          <div className='row row-eq-height align-items-center'>
            <div className='col align-self-center'>
              {fraseFiltrada.map(fraseFiltrada => <h1 style={{fontSize:30}} key={fraseFiltrada.Autores}><FontAwesomeIcon icon={faQuoteLeft} /> {fraseFiltrada.frases}. <FontAwesomeIcon icon={faQuoteRight} /></h1>)}<br></br>
              {fraseFiltrada.map(fraseFiltrada => <h3 className='text-right' style={{fontSize:12}} key={fraseFiltrada.Autores}><em>{fraseFiltrada.Autores}</em></h3>)} 
              
            </div>
            </div>
          <div className='row mt-auto' style={{height: 'auto',position:'absolute',bottom:'10%',left:'80%'}}>
            <div className= 'col align-self-end'>
              <button className='btn btn-success' onClick={this.click} >Nueva frase</button>
          </div>
          <div className='row mt-auto' style={{height: 'auto',position:'absolute',bottom:'0%',left:'-550%'}}>
            <div className= 'col align-self-end'>
           
            <a href={`https://twitter.com/intent/tweet?text="${twittear.frases +'" '+twittear.Autores}`} style={{color: 'white'}}><button className='btn btn-success' > <FontAwesomeIcon icon={faTwitter} /></button></a>
          </div>
          </div>
        </div>
        </div>
         <div style={{width:'100%',color:'black', textAlign:'center',position:'absolute', bottom:'6%'}}><a href='https://github.com/frann11'><FontAwesomeIcon className='github' icon={faGithub} size={'3x'} /> </a> </div>
        <em><div className='creditos' style={{position:'absolute', bottom:0,width:'100vw',backgroundColor:'black',color:'white',textAlign:'right',}}><h6 style={{marginRight:'1%', marginTop:5,fontSize:13}}>Con <FontAwesomeIcon icon={faHeart} /> franchu</h6></div></em>
      </div>
      );
    }
  }
  
  export default App;
  